.profile {
	width: 250px;
	height: 330px;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	border: 1px solid gray;
	box-shadow: 0 0 10px 3px gray;
	border-radius: 5px;
	font-size: 12px;
color: gray;
overflow: hidden;
text-transform: none;
margin-bottom: 30px;
}
.description {
	margin-top: 30px;
	display: flex;
	flex-direction: column;
	align-items: center;
}
.avatar {
	border: 1px solid rgb(216, 215, 215);
	border-radius: 50%;
	width: 100px;
	height: 100px;
	margin-bottom: 30px;
}
.name {
	font-size: 14px;
	font-weight: 700;
	color: black;
	margin-bottom: 10px;
}
.tag {
	margin-bottom: 10px;
}

.location{
	margin-bottom: 10px;
}
.stats {
	display: flex;
	justify-content: center;
	width: 100%;
	height: 70px;
	list-style: none;
}
.stats li{
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	width: 100%;
	height: 100%;
	border: 1px solid rgb(216, 215, 215);
background: rgba(219, 219, 219, 0.521);
}
.label {
	text-transform: none;
margin-bottom: 8px;
}
.quantity {
	color: black;
	font-weight: 700;

}
