.statistics {
	width: 270px;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	align-items: center;
	border: 1px solid gray;
	box-shadow: 0 0 10px 3px gray;
	border-radius: 5px;
	overflow: hidden;
	margin-bottom: 30px;
}
.title {
	height: 70px;
	display: flex;
	align-items: center;
	justify-content: center;
	text-transform: uppercase;
	font-size: 14px;
}
.statList {
	display: flex;
	justify-content: center;
	align-items: center;
	width: 100%;
	height: 60px;
}
.item {
	width: 100%;
	height: 100%;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	color: white;
	background: #000;
}
.label {
	font-size: 10px;
	text-transform: lowercase;
	margin-bottom: 10px;
}
.percentage {
	font-size: 12px;
	font-weight: bold;
	text-transform: uppercase;
}
